.stage-cube-cont{
    width: 50%;
    height: 100%;
    top: 0;
    padding-top: 18%;
    margin-left: 0;
    position: absolute;
    right: 0;
    overflow: hidden;
}

.cubespinner{
    animation-name: spincube;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-duration: 12s;
    transform-style: preserve-3d;
    transform-origin: 100px 100px 0;
    margin-left: calc(50% - 100px);

    div{
        position: absolute;
        width: 200px;
        height: 200px;
        border: 1px solid #ccc;
        background: rgba(255, 255, 255, 0.4);
        text-align: center;
        font-size: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: 0 0 20px 0px lightyellow;
    }

    .face1 {
        transform: translateZ(100px);
        color: #dd0031;
      }
      .face2 {
        transform: rotateY(90deg) translateZ(100px);
        color: #f06529;
      }
      .face3 {
        transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
        color: #28a4d9;
      }
      .face4 {
        transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
        color: #5ed4f4;
      }
      .face5 {
        transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);
        color: #efd81d;
      }
      .face6 {
        transform: rotateX(-90deg) translateZ(100px);
        color: #ec4d28;
      }


}

@keyframes spincube {
    from,
    to {
      transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    }
    16% {
      transform: rotateY(-90deg) rotateZ(90deg);
    }
    33% {
      transform: rotateY(-90deg) rotateX(90deg);
    }
    50% {
      transform: rotateY(-180deg) rotateZ(90deg);
    }
    66% {
      transform: rotateY(-270deg) rotateX(90deg);
    }
    83% {
      transform: rotateX(90deg);
    }
  }


  .me {
    padding: 15px;
    margin-bottom: 15px;
    border-left: 4px solid #007bff;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    font-size: 15px;
    text-align: justify;
    text-justify: inter-word;
    color: #8d8d8d;
  }
  
  .me:first-of-type {
    border-left-color: #C0C0C0;
    font-weight: bold;
  }

  @media screen and (max-width: 1200px) {
    .stage-cube-cont {
      width: 80%; /* Ajuste para dar ms espacio */
      height: auto; /* Ajustar altura automticamente */
      padding-top: 10%; /* Reducir el padding superior */
      margin: 0 auto; /* Centrar el contenedor */
      position:relative;
      top: auto;
      right: auto;
      overflow: visible;
    }
  
    .cubespinner div {
      
      animation-timing-function: ease-in-out;
    animation-duration: 12s;
    transform-style: preserve-3d;
    transform-origin: 100px 100px 0;
    margin-left: calc(50% - 100px);
    }
  }
  
  @media screen and (max-width: 600px) {
    .stage-cube-cont {
      width: 90%; /* Ms espacio en dispositivos muy pequeos */
      padding-top: 5%; /* Reducir an ms el padding superior */
    }
  
    .cubespinner div {
    animation-timing-function: ease-in-out;
    animation-duration: 12s;
    transform-style: preserve-3d;
    transform-origin: 100px 100px 0;
    margin-left: calc(50% - 100px);
    }
  }
  